import { FileInfo } from 'ssh2-sftp-client';

export interface SftpInterface {
  init(): Promise<void>;

  writeFile(destination: string, content: string): Promise<string>;

  readFile(destination: string): Promise<Buffer>;

  moveFile(path: string, file: FileInfo, destination: string): Promise<void>;

  deleteFile(path: string): Promise<void>;

  list(destination: string): Promise<Array<FileInfo>>;
}
